import {Header} from "./components/Header";
import {Main} from "./sections/Main";
import {Container} from "./components/Container";
import {About} from "./sections/About";
import {How} from "./sections/How";
import {Calculator} from "./sections/Calculator";
import {Guide} from "./sections/Guide";
import {Footer} from "./components/Footer";
import {Stake} from "./sections/Stake";
import {Faq} from "./sections/Faq";
import {Volume} from "./sections/Volume";
import {Brics} from "./sections/Brics";
import {Why} from "./sections/Why";
import {Welcome} from "./sections/Welcome";
import {Referral} from "./sections/Referral";
import {Media} from "./sections/Media";
import {useLocation} from "react-router-dom";
import Cookies from 'universal-cookie'
import {useEffect} from "react";
import CookieConsent from "react-cookie-consent"
import {useTranslation} from "react-i18next"


function MainPage() {

    const search = useLocation().search
    const ref = new URLSearchParams(search).get('ref')
    const { t } = useTranslation()

    const setRef = () => {
        const cookies = new Cookies();
        console.log('search ref', ref)
        if (ref !== undefined && ref !== null && ref !== 'true') {
            cookies.set('ref', ref, { path: '/'})
        } else {
            const refCookie = cookies.get('ref')
            console.log('refCookie', refCookie)
            if (refCookie !== undefined && refCookie !== null && refCookie !== 'true') {
                window.location.search = `?ref=${refCookie}`
            }
        }
    }

    useEffect(() => {
        setRef()
    }, [ref])

    return (
        <>
        
            <Header/>
            <Container>
                <Welcome/>
            </Container>
            <Stake/>
            
            <Container>
                {/* <Stake/> */}
                
                <Calculator/>
                <Referral />
                <Media />
            </Container>
            <Footer/>
            <CookieConsent
                location="bottom"
                buttonText={t("cookies.button")}
                cookieName="ref"
                contentStyle={{margin: "0", flex: "unset"}}
                style={{ background: "linear-gradient(92.32deg, #2E22AC 0%, #DB499E 98.07%)", alignItems: 'center', padding: "15px", boxSizing: "border-box", gap: "10px" }}
                buttonStyle={{ color: "#DB499E", fontSize: "13px", background: "#fff", borderRadius: "8px", fontFamily: "Mulish", fontWeight: "600", boxSizing: "border-box", margin: "0" }}
            >
                {t("cookies.text")}
            </CookieConsent>
        </>
    );
}

export default MainPage
