import {useEffect, useRef, useState} from "react";
import {GradientButton} from "../components/GradientButton";
import {PLATFORM_LINK} from "../config/constants";
import {useRefLink} from "../hooks/useRefLink";
import {useTranslation} from "react-i18next";
import { isApp } from "../routes";

export const Calculator = () => {

    const [amount, setAmount] = useState(0.1)
    const [term, setTerm] = useState(7)
    const [amountInputStep, setAmountInputStep] = useState(0.01)
    const [profit, setProfit] = useState(0)
    const [percentage, setPercentage] = useState(0)
    const [total, setTotal] = useState(0)
    const [interestRate, setInterestRate] = useState(0)
    const [reinvest, setReinvest] = useState(false)

    const [openPlatformWithRefLink] = useRefLink()
    const { t } = useTranslation()

    const amountInputRef = useRef()
    const termInputRef = useRef()

    const amountChangeHandler = (e) => {
        const value = e.target.value
        if (parseFloat(value) < 50) {
            setAmount(value)
        } else {
            setAmount(parseFloat(value).toFixed(0))
        }
    }

    const termChangeHandler = (e) => {
        setTerm(e.target.value)
    }

    const amountInputChangeHandler = (e) => {
        setAmount(e.target.value)
    }

    const termInputChangeHandler = (e) => {
        setTerm(e.target.value)
    }

    const changeAmountInputStep = () => {
        const value = parseFloat(amount)
        if (value >= 100) {
            setAmountInputStep(10)
        } else if (value >= 50) {
            setAmountInputStep(1)
        } else if (value >= 10) {
            setAmountInputStep(0.1)
        } else setAmountInputStep(0.01)
    }

    const reinvestToggleHandler = () => {
        setReinvest(!reinvest)
    }

    const calculateInterestRate = () => {
        if (amount >= 80) {
            setInterestRate(0.05)
        } else if (amount >= 30) {
            setInterestRate(0.04)
        }else if (amount >= 2) {
            setInterestRate(0.03)
        } else setInterestRate(0.02)
    }

    const calculate = () => {
        let tempProfit = 0.0
        let tempTotal = 0.0
        let tempPercentage = 0.0

        if (term > 0 && amount > 0) {

            if (reinvest) {
                for (let day = 1; day <= term; ++day) {
                    tempProfit += (parseFloat(amount) + tempProfit) * interestRate
                }
            } else {
                for (let day = 1; day <= term; ++day) {
                    tempProfit += parseFloat(amount) * interestRate
                }
            }

            tempTotal = parseFloat(amount) + parseFloat(tempProfit)
            tempPercentage = ((tempTotal / parseFloat(amount)) * 100) - 100
        }



        tempProfit = parseFloat(tempProfit).toFixed(2)
        setProfit(tempProfit)
        tempTotal = parseFloat(tempTotal).toFixed(2)
        setTotal(tempTotal)
        tempPercentage = parseFloat(tempPercentage).toFixed(2)
        setPercentage(tempPercentage)

    }

    useEffect(() => {
        changeAmountInputStep()
    }, [amount, amountInputStep])

    useEffect(() => {
        calculateInterestRate()
    }, [amount])

    useEffect(() => {
        calculate()
    }, [amount, term, reinvest, interestRate])


    return (
        <section className="calculator" id="calculator">
            <h2 className="title">{t("Profit calculator")}</h2>
            <p className={isApp() ? "subtitle" : "subtitle alignLeft"} >
                {t("Calculate how much you can earn with Big Whale")}
            </p>
            <div className="calculator__container">
                <div className="calculator__container__column">
                    <div className="ranges">
                        <div className="range__wrapper">
                            <p className="range__title">
                                {t("Amount of investment")}
                            </p>
                            <div className="range__value__wrapper">
                                <input type="text"
                                       className="range__value__input"
                                       value={amount}
                                       onChange={amountInputChangeHandler}
                                       ref={amountInputRef}
                                       style={{ width: amountInputRef.current ? amountInputRef.current.value.length + 0.2 + 'ch' : 'auto' }}
                                       maxLength="10"
                                />
                                <p className="range__value">
                                    BNB
                                </p>
                            </div>
                            <div className="input__range__wrapper">
                                <input type="range"
                                       className="range"
                                       onChange={amountChangeHandler}
                                       value={amount}
                                       min={0.1}
                                       max={1010}
                                       step={amountInputStep}
                                />
                                <div className="range__labels">
                                    <div className="range__labels__item">0.1</div>
                                    <div className="range__labels__item">1000</div>
                                </div>
                            </div>
                        </div>
                        <div className="range__wrapper">
                            <p className="range__title">
                                {t("Investment period")}
                            </p>
                            <div className="range__value__wrapper">
                                <input type="text"
                                       className="range__value__input"
                                       value={term}
                                       onChange={termInputChangeHandler}
                                       ref={termInputRef}
                                       style={{ width: termInputRef.current ? termInputRef.current.value.length + 0.2 + 'ch' : 'auto' }}
                                       maxLength="10"
                                />
                                <p className="range__value">
                                    Days
                                </p>
                            </div>
                            <div className="input__range__wrapper">
                                <input type="range"
                                       className="range"
                                       onChange={termChangeHandler}
                                       value={term}
                                       min={1}
                                       max={365}
                                       step={1}
                                />
                                <div className="range__labels">
                                    <div className="range__labels__item">1</div>
                                    <div className="range__labels__item">365</div>
                                </div>
                            </div>
                        </div>

                        <div className="calculations_mobile">

                            <table className="calculations__table">
                                <tr>
                                    <td>
                                        <div className="calculations__item">
                                            <div className="calculations__item__title">
                                                {t("Profit")}
                                            </div>
                                            <div className="calculations__item__text">
                                                + {profit} BNB
                                            </div>
                                        </div>
                                    </td>
                                </tr>
                                <tr>
                                    <td>
                                        <div className="calculations__item">
                                            <div className="calculations__item__title">
                                                {t("Period")}
                                            </div>
                                            <div className="calculations__item__text">
                                                {term} {t("days")}
                                            </div>
                                        </div>
                                    </td>
                                </tr>
                                <tr>
                                    <td>
                                        <div className="calculations__item">
                                            <div className="calculations__item__title">
                                                14.00
                                            </div>
                                            <div className="calculations__item__text">
                                                {percentage}%
                                            </div>
                                        </div>
                                    </td>
                                </tr>
                                <tr>
                                    <td>
                                        <div className="calculations__item">
                                            <div className="calculations__item__title">
                                                {t("Total balance")}
                                            </div>
                                            <div className="calculations__item__text">
                                                {total} BNB
                                            </div>
                                        </div>
                                    </td>
                                </tr>
                            </table>

                            <div className="reinvest">
                                <p className="reinvest__label">
                                    {t("Reinvest")}
                                </p>
                                <label className="switch">
                                    <input type="checkbox" checked={reinvest} onChange={reinvestToggleHandler}/>
                                    <span className="slider round"/>
                                </label>
                            </div>

                        </div>

                    </div>
                    {
                    isApp() ?
                    <GradientButton
                        text={t("Start Earning")}
                        onClick={openPlatformWithRefLink}
                    /> : <></>
                    }
                </div>

                <div className="calculations">

                    <table className="calculations__table">
                        <tr>
                            <td>
                                <div className="calculations__item">
                                    <div className="calculations__item__title">
                                        {t("Profit")}
                                    </div>
                                    <div className="calculations__item__text">
                                        + {profit} BNB
                                    </div>
                                </div>
                            </td>
                            <td>
                                <div className="calculations__item">
                                    <div className="calculations__item__title">
                                        {t("Period")}
                                    </div>
                                    <div className="calculations__item__text">
                                        {term} {t("days")}
                                    </div>
                                </div>
                            </td>
                        </tr>
                        <tr>
                            <td>
                                <div className="calculations__item">
                                    <div className="calculations__item__title">
                                        {t("Profit percentage")}
                                    </div>
                                    <div className="calculations__item__text">
                                        {percentage}%
                                    </div>
                                </div>
                            </td>
                            <td>
                                <div className="calculations__item">
                                    <div className="calculations__item__title">
                                        {t("Total balance")}
                                    </div>
                                    <div className="calculations__item__text">
                                        {total} BNB
                                    </div>
                                </div>
                            </td>
                        </tr>
                    </table>

                    <div className="reinvest">
                        <p className="reinvest__label">
                            {t("Reinvest")}
                        </p>
                        <label className="switch">
                            <input type="checkbox" checked={reinvest} onChange={reinvestToggleHandler}/>
                                <span className="slider round"/>
                        </label>
                    </div>

                </div>
            </div>
        </section>
    )
}
