import {GradientButton} from "../components/GradientButton";
import {PLATFORM_LINK} from "../config/constants";
import {useRefLink} from "../hooks/useRefLink";
import {useTranslation} from "react-i18next";
import { Container } from "../components/Container";
import referral_cat from '../assets/img/how-can-i-join.png'

export const Referral = () => {

    const [openPlatformWithRefLink] = useRefLink()
    const { t } = useTranslation()

    return (
        <section className="referral" id="cat-bonus">
            <Container>
            <h2 className="title">
                Big Whale Bonus
            </h2>
            <p className="subtitle">
            Get your referral bonus from profits of invited partners
            </p>
            <div class="referral__row">
                <table class="referral__table">
                    <thead>
                        <tr>
                            <th>Level</th>
                            <th>Network</th>
                            <th>Volume</th>
                            <th>Profit</th>
                        </tr>
                    </thead>
                    <tbody>
                        <tr class="referral-table-row">
                            <td class="referral-table-row__lvl__td">-</td>
                            <td class="referral-table-row__network">-</td>
                            <td class="referral-table-row__amount">-</td>
                            <td class="referral-table-row__amount">-</td>
                        </tr>
                    </tbody>
                </table>
                <div></div>
                <div class="referral-link__card">
                    <p class="referral-link__card__title">Total Big Whale bonus<span class="accent">- BNB</span></p>
                    <div class="kPGecBCs">
                    <GradientButton
                        text={t("Learn more")}
                        type={"w-100"}
                        
                    />
                    </div>
                    <div class="separator"></div>
                    <div class="referral-link__wrapper">
                        <p class="referral-link">Connect your wallet first</p>
                        <div class="kPGecBCs eaZyXO0z">
                            <GradientButton
                                text={t("Learn more")}
                                type={"w-100"}
                                disabled={true}  
                            />
                        </div>
                    </div>
                </div>
                <img src={referral_cat} alt="" class="referral__img"/>
            </div>
            </Container>
        </section>
    )
}
