import {GradientButton} from "../components/GradientButton";
import cat from '../assets/img/contact.png'
import {toast} from "react-hot-toast";
import {TELEGRAM_LINK} from "../config/constants";
import {useTranslation} from "react-i18next";

export const ContactUs = () => {

    const { t } = useTranslation()

    const copyLink = () => {
        window.dataLayer.push({'event': 'Support', 'type': 'email'})
        navigator.clipboard.writeText("support@mycat.team").then(() => {
            toast(`${t("contact.message_on_copy_email")}`, {icon: '💾'})
        })
    }

    return (
        <section className="contact">
            <h2 className="title">
                {t("Still have any questions?")}
            </h2>
            <div className="contact__row">
                <div className="contact__column">
                    <GradientButton
                        text="Telegram"
                        style={{width: 315, height: 50}}
                        textStyle={{fontSize: 22, lineHeight: '32px'}}
                        onClick={() => {
                            window.dataLayer.push({'event': 'Support', 'type': 'telegram'})
                            window.open(TELEGRAM_LINK)
                        }}
                    />
                    <GradientButton
                        text="support@mycat.team"
                        style={{width: 315, height: 50}}
                        textStyle={{fontSize: 22, lineHeight: '32px'}}
                        onClick={copyLink}
                    />
                </div>
                <img src={cat} alt="" className="contact__img"/>
            </div>
        </section>
    )
}
