import {Container} from "./Container";
import {useState} from "react";
import close from '../assets/svg/close.svg'
import logo from '../assets/img/logoWithText.png'
import menu from '../assets/svg/menu.svg'
import {FAQ_LINK, LEARN_MORE, NOTION_LINK, SUPPORT_LINK, TELEGRAM_LINK, YOUTUBE_LINK} from "../config/constants";
import {LocalizationButton} from "./LocalizationButton";
import {GradientButton} from "./GradientButton";
import {useRefLink} from "../hooks/useRefLink";
import {useTranslation} from "react-i18next";
import { isApp } from "../routes";

export const Header = () => {

    const [isOpen, setIsOpen] = useState(false)

    const [openPlatformWithRefLink] = useRefLink()
    const { t } = useTranslation()

    const openMenu = () => {
        setIsOpen(!isOpen)
    }

    return (
        <header>
            <Container>
                {/*{isOpen ? (*/}
                {/*    <>*/}
                        <div className={isOpen ? "menu menu_open" : "menu"}>
                            <div className="menu__header" onClick={openMenu}>
                                <a href="/">
                                    <img src={logo} alt="logo" className="header_mobile__logo"/>
                                </a>
                                <img src={close} alt="close" className="menu__btn"/>
                            </div>
                            <div className="navbar">
                                <a href="/" className="nav__link" onClick={openMenu}>{t("Home")}</a>
                               
                                <a href="#calculator" className="nav__link" onClick={(e) => {
                                    e.preventDefault()
                                    window.dataLayer.push({'event': 'Calculator'})
                                    openMenu()
                                    document.getElementById('calculator').scrollIntoView({
                                        behavior: 'smooth',
                                        block: 'start'
                                    })
                                }}>{t("Calculator")}</a>
                                <a href={LEARN_MORE} target="_blank" className="nav__link" onClick={openMenu}>
                                    {t("CAT bonus")}
                                </a>
                                <a href="#our-mission" className="nav__link" onClick={(e) => {
                                    e.preventDefault()
                                    openMenu()
                                    document.getElementById('our-mission').scrollIntoView({
                                        behavior: 'smooth',
                                        block: 'start'
                                    })
                                }}>{t("Our mission")}</a>
                               
                                <a href={FAQ_LINK} className="nav__link" onClick={(e) => {
                                    e.preventDefault()
                                    openMenu()
                                    document.getElementById('faq').scrollIntoView({
                                        behavior: 'smooth',
                                        block: 'start'
                                    })
                                }}>{t("Faq")}</a>
                                <a href={SUPPORT_LINK} target="_blank" className="nav__link"
                                   onClick={openMenu}>{t("Support")}</a>
                                <GradientButton
                                    text={isApp() ? t("Start Earning") : t("Connect Wallet")}
                                    onClick={openPlatformWithRefLink}
                                    type="outlined"
                                />
                            </div>
                            <div className="menu__social-links__container">
                                <div className="social-links">
                                    <a href={TELEGRAM_LINK} target="_blank" className="social-links__item">
                                        <img src={require("../assets/svg/telegram logo.svg").default}
                                             className="social-links__item__img" alt="telegram"/>
                                    </a>
                                    <a href={NOTION_LINK} target="_blank" className="social-links__item">
                                        <img src={require("../assets/svg/Notion logo.svg").default}
                                             className="social-links__item__img" alt="notion"/>
                                    </a>
                                    <a href={YOUTUBE_LINK} target="_blank" className="social-links__item">
                                        <img src={require("../assets/svg/youtube header logo.svg").default}
                                             className="social-links__item__img" alt="notion"/>
                                    </a>
                                    <a href="#" className="social-links__item social-links__item_soon">
                                        <img src={require("../assets/svg/discordapp logo.svg").default}
                                             className="social-links__item__img"
                                             alt="discord"/>
                                        <img src={require("../assets/svg/soon.svg").default} alt=""/>
                                    </a>
                                    <a href="#" className="social-links__item social-links__item_soon">
                                        <img src={require("../assets/svg/WeChat logo.svg").default}
                                             className="social-links__item__img" alt="WeChat"/>
                                        <img src={require("../assets/svg/soon.svg").default} alt=""/>
                                    </a>
                                </div>
                            </div>
                        </div>
                        <div className={isOpen ? "menu-bg menu-bg_open" : "menu-bg"}  onClick={openMenu}/>
                        <div className="header_mobile">
                            <a href="/">
                                <img src={logo} alt="logo" className="header_mobile__logo"/>
                            </a>
                            <div className="header_mobile__row">
                                {/* <LocalizationButton/> */}
                                <img src={menu} alt="menu" className="menu__btn" onClick={openMenu}/>
                            </div>
                        </div>
                        <div className="header__wrapper">
                            <div className="header__row">
                                <button className="menu__btn">
                                    <img src={menu} alt="" className="header__menu__img" onClick={openMenu}/>
                                    <a href="/" className="">
                                        <img src={logo} alt="logo" className="header__logo"/>
                                    </a>
                                </button>
                            </div>
                            {/* <a href="/" className="header__logo__link">
                                <img src={logo} alt="logo" className="header__logo"/>
                            </a> */}
                            <div className="header__row">
                                <div className="social-links">
                                    <a href={TELEGRAM_LINK} target="_blank" className="social-links__item">
                                        <img src={require("../assets/svg/telegram logo.svg").default}
                                             className="social-links__item__img" alt="telegram"/>
                                    </a>
                                    <a href={NOTION_LINK} target="_blank" className="social-links__item">
                                        <img src={require("../assets/svg/Notion logo.svg").default}
                                             className="social-links__item__img" alt="medium"/>
                                    </a>
                                    <a href={YOUTUBE_LINK} target="_blank" className="social-links__item">
                                        <img src={require("../assets/svg/youtube header logo.svg").default}
                                             className="social-links__item__img" alt="notion"/>
                                    </a>
                                    <a href="#" className="social-links__item social-links__item_soon">
                                        <img src={require("../assets/svg/discordapp logo.svg").default}
                                             className="social-links__item__img"
                                             alt="discord"/>
                                        <img src={require("../assets/svg/soon.svg").default} alt=""/>
                                    </a>
                                    <a href="#" className="social-links__item social-links__item_soon">
                                        <img src={require("../assets/svg/WeChat logo.svg").default}
                                             className="social-links__item__img" alt="WeChat"/>
                                        <img src={require("../assets/svg/soon.svg").default} alt=""/>
                                    </a>
                                </div>
                                <div className="header__btn__container">
                                    <GradientButton
                                        text={isApp() ? t("Start Earning") : t("Connect Wallet")}
                                        onClick={openPlatformWithRefLink}
                                        type="outlined"
                                    />
                                </div>
                            </div>
                        </div>
            </Container>                      
        </header>
    );
}
